.MuiPaper-root {
	min-height: 100vh;
}
.loader {
	width: 4.5rem;
	height: 4.5rem;
	margin: 2vh;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
}

.posts {
	@media screen and (max-width: 400px) {
		padding: 2px;
	}
	padding: 10px;
	width: fit-content;
	text-align: center;
	display: flex;
}

.posts-wrapper {
	width: 100%;
}

.posts .user-image {
	height: 60px;
	width: 60px;
	border-radius: 50%;
	object-fit: cover;
}

.circular {
	animation: rotate 2s linear infinite;
	height: 100px;

	width: 100px;
	text-align: center;
}

.path {
	stroke-dasharray: 1, 200;
	stroke-dashoffset: 0;
	stroke: #b6463a;
	animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
	stroke-linecap: round;
}

@keyframes rotate {
	100% {
		transform: rotate(360deg);
	}
}
@keyframes dash {
	0% {
		stroke-dasharray: 1, 200;
		stroke-dashoffset: 0;
	}
	50% {
		stroke-dasharray: 89, 200;
		stroke-dashoffset: -35;
	}
	100% {
		stroke-dasharray: 89, 200;
		stroke-dashoffset: -124;
	}
}
@keyframes color {
	100%,
	0% {
		stroke: red;
	}
	40% {
		stroke: blue;
	}
	66% {
		stroke: green;
	}
	80%,
	90% {
		stroke: yellow;
	}
}
.post-container {
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
	justify-content: center;
}
.video-container {
	margin-top: 1.5vh;
	height: 80vh;
	width: 100%;
	max-width: 300px;
	max-height: 800px;
	@media screen and (min-width: 768px) {
		max-width: 350px;
	}
	border-radius: 20px;
	background-color: transparent;
	overflow: scroll;
	scroll-snap-type: y mandatory;
	scrollbar-width: none;
	margin-left: auto;
	margin-right: auto;
}
/* .post-left-container { */
/* width: 35%; */
/* } */
.post-right-container {
	overflow-x: scroll;
	padding-top: 0.5rem;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}
.post-container{
	padding-bottom: 4rem;
}

.posts{
	display: none;
	@media screen and (max-width: 970px) {
		display: block;
	}
	padding-left: auto;
	padding-right: auto;
}

.posts-wrapper{
	width: 6rem;
	height: 8rem;
	overflow-x: hidden;
	overflow-y: hidden;
}

.post-right-container::-webkit-scrollbar {
	display: none;
}
.post-container::-webkit-scrollbar {
	display: none;
}
.video-container::-webkit-scrollbar {
	display: none;
}

.video-user-container{
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	width: 100%;
}

.video-left-container{
	width: 100%;
	@media screen and (max-width: 970px) {
		display: none;
	}
}



.video-right-container{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
	gap: 1rem;
	overflow-x: hidden;
	@media screen and (max-width: 970px) {
		display: none;
	}
}

.suggested-users-heading{
	width: 100%;
	text-align: center;
	box-sizing: border-box;
	padding-left: 10%;
	@media screen and (max-width: 970px) {
		display: none;
	}
}

.suggested-users{
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	@media screen and (max-width: 970px) {
		display: none;
	}
}

.suggested-users-wrapper{
	
	width: 100%;
	padding: 0.5rem;
	overflow-x: hidden;
	overflow-y: scroll;
	display: flex;
	justify-content: space-evenly;
	align-items: flex-start;
	
	
}
.suggested-users-img{
	height: 100%;
	width: 20%;
	display: flex;
	justify-content: flex-end;
	
}

.suggested-users-img > img{
	height: 3rem;
	width: 3rem;
}

.suggested-users-details{
	height: 100%;
	width: 50%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
}

.suggested-users-view{
	height: 100%;
	width: 20%;
	display: flex;
	align-items: center;
	justify-content: center;
	
	margin-top: auto;
	margin-bottom: auto;
}

.suggested-users-view > button{
	background-color:  #0095f6;
	color: white;
	border: none;
	outline: none;
	width: 4rem;
	height: 2rem;
	border-radius: 0.5rem;
}

.suggested-users-wrapper::-webkit-scrollbar {
	display: none;
}

.feed-no-post{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	margin-top: 5rem;
	width: 100%;
}

.dark.feed-no-post {
	// background: #0a1720;
	p {
		color: white;
	}
	img.display-image {
		filter: invert(1);
	}
}

.videos {
	height: 100%;
	position: relative;
}
.fa {
	position: absolute;
	bottom: 3%;
	left: 5%;
	display: flex;
	color: white;
	font-family: sans-serif;
	align-items: center;
}
.img_reel {
	height: 2.5rem;
	width: 2.5rem;
	border-radius: 50%;
	object-fit: cover;
	margin-right: 0.5rem;
	margin-bottom: 1rem;
}
.name {
	margin-bottom: 1rem;
}
.like {
	position: absolute;
	bottom: 12%;
	right: 5%;
}
.likes {
	color: white;
	margin: 0 auto;
	text-align: center;
}
.liked {
	color: red;
	font-size: 1.5rem;
}
.dislike {
	color: white;
	font-size: 1.5rem;
}
.chat {
	color: white;
	font-size: 1.4rem;
	position: absolute;
	bottom: 5%;
	right: 5%;
}
